.ba-about {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  min-height: 555px;
}

.ba-about__banner {
  background: #9f9acf url("../img/about-us.png") 50% no-repeat;
  background-size: cover;
  width: 380px;
  visibility: hidden;
}

.ba-about__banner--visible {
  visibility: visible;
}

.ba-about__header {
  padding: 75px 10px 10px;
}

.ba-about__icon {
  background: url("../img/icon-aboutus.png") 50% no-repeat;
  width: 50px;
  height: 54px;
}

.ba-about__title {
  padding-top: 40px;
}

.ba-about__description {
  max-width: 670px;
  padding-top: 76px;
}

.ba-about__text {
  font-size: 20px;
}

.ba-about__btn {
  border-color: #7d7a7c;
  color: #7d7a7c;
  margin-top: 90px;
}

@media all and (max-width: 1120px) {
  .ba-about {
    justify-content: center;
  }
}
